import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Constants } from 'core/constants';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { group } from 'console';

@Component({
  selector: 'app-gym-safty',
  templateUrl: './gym-safty.component.html',
  styleUrls: ['./gym-safty.component.css']
})
export class GymSaftyComponent implements OnInit,HttpSuccesFailureResponse {

  constructor(private route : ActivatedRoute,private http_user: HttpUserAuthUtilsService, private http_common: HttpCommonUtilsService,private router: Router) { }
  allGymList: AllGymResponseDetail[]= [];
  sub:any;
  gymId:any;
  isEditing:boolean = false; 
  public gymSaftyForm: FormGroup
  public questionForm: FormGroup;
  covidSafetyResponseData = [];



  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.gymId = +params['id'];
      if(!this.gymId||this.gymId==0){
      this.setupFormControl();
      }
  });

    this.http_user.getAllGymList(0,0, this,true);

  }

  private setupFormControl(){
    this.gymSaftyForm = new FormGroup({
      gymId: new FormControl("Select",Validators.required) ,
      questions:new FormArray([]),
    });

  }

  

  submit(){

    if(this.gymSaftyForm.valid){ 
      let questionList = [];
      for (let i = 0; i < this.questions.length; i++) {
        let control = this.questions.controls[i];
        // if(control.get("answer").value&&control.get("answer").value.length>0){
        //   let ques = {"question":control.get("question").value,"answer":control.get("answer").value,"questionId":control.get("id").value}
        //   questionList.push(ques);
        // }
        let ques = {"question":control.get("question").value,"answer":control.get("answer").value,"questionId":control.get("id").value}
        questionList.push(ques);
      }
      if(questionList.length==0){
        alert("Please Add answer for few question before submiting.");
        return
      }
      else{ 
        let request = {"covidQuestionAnswerDto":questionList}
        this.http_common.saveGymCovidSafetyMeasures(request,this.gymId,this);
      }
    }else{
this.markFormGroupTouched(this.gymSaftyForm);
window.scroll(0, 0);
}
}

goToHome(){
  this.router.navigate(['/']);
}

selectGym(event,value){
   this.setupFormControl();
 this.gymSaftyForm.patchValue({gymId:value})
  if(value != 'Select'){ 
    this.gymId = value;
    //this.http_common.getGymCovidSafetyMeasures(value,this);
  }
}



  private  addQuestions(question,answer,id){
    const group = new FormGroup({
      question: new FormControl(question,Validators.required),
      answer: new FormControl(answer),
      id: new FormControl(id,Validators.required)

    });

    this.questions.push(group);
  }

  get questions() {
    return this.gymSaftyForm.get('questions') as FormArray;
  }


  onSuccess(type: any, responsedata: any) {
   switch(type){
     case Constants.AllGymAPI:
       this.allGymList = responsedata.data;
       break;
      
      case Constants.covidgymQAAPI:
        this.covidSafetyResponseData = responsedata.data;
        this.covidSafetyResponseData.forEach(
          (value)=>{ console.log(value.question)
            this.addQuestions(value.question , value.answer , value.questionId) 
          }
        );
        break;
      
      case Constants.saveGymCovidQA:
      alert("Response saved successfully")
      this.setupFormControl();

      // window.location.reload();
        break;
   }
  }
  onFailure(type: any, response: any) {
    switch(type){
      case Constants.AllGymAPI:
        console.log(response)
        break;
      case Constants.covidgymQAAPI:
        console.log(response)
        break;
      case Constants.saveGymCovidQA:
        console.log(response)
        break;
    
    }  
  }
  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }


  }
}



